import React from 'react'
import MessageBox from '../components/MessageBox';

function Ending({background: BackgroundImage}) {
    const background = {
        backgroundImage: `url(${BackgroundImage})`
    }
    return (
        <div className="container ending" style={background}>
            <MessageBox type="static" text={["Being with you is something I want to do the rest of my life, it will undoubtedly be my greatest achievement, and it will be one I will remain proud and happy about, when I am with you I am lost in time and space, every minute spent with you is glorious, I really can’t do without you, you have become an integral part of me, I love you so much my sweetie 3.14",
            3000, 
            "You are the reason I breathe, I'm so into you and I'm sure I will lose myself if I lose you, I am not afraid to say it, I will do whatever you want me to do, I just really can’t Imagine myself without you, I can say it a zillion times that your love is great, I have seen love but nothing like this, this love is exceptional, it’s out of this world.",
            3000,
            "I will love you always. You are now officially a lady that I'll spend my life with. I love you so much Sharlyn. I'll build a great future for the both of us. Me and You vs. The World right? Till death do us part. - Truly yours, Cyrus"
            ]} />
        </div>
    )
}

export default Ending
